import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/HelloFromVux'
import Alert from '@/components/alert'
import Scoller  from '@/components/scoller'
import XDialog  from '@/components/XDialog'
import popupPicker  from '@/components/popupPicker'
import confirm from '@/components/confirm'
import popup from '@/components/popup'
import datepicker from '@/components/datepicker'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/alert',
      name: 'alert',
      component: Alert
    },
    {
      path: '/scoller',
      name: 'scoller',
      component: Scoller
    },
    {
      path: '/xdialog',
      name: 'xdialog',
      component: XDialog
    },
    {
      path: '/popupPicker',
      name: 'popupPicker',
      component: popupPicker
    },
    {
      path: '/confirm',
      name: 'confirm',
      component: confirm
    },
    {
      path: '/popup',
      name: 'popup',
      component: popup
    },
    {
      path: '/datepicker',
      name: 'datepicker',
      component: datepicker
    }



  ]
})



