        value: 'sz',
        parent: 'china001'
      }, {
        name: '001',
        value: 'gx001',
        parent: 'china002'
      }, {
        name: '002',
        value: 'gx002',
        parent: 'china002'
      }, {
        name: '001_001',
        value: '0003',
        parent: 'usa001'
      }, {
        name: '001_002',
        value: '0004',
        parent: 'usa001'
      }, {
        name: '002_001',
        value: '0005',
        parent: 'usa002'
      }, {
